#left-wrapper {
    position: fixed;
    display: flex;
    flex-direction: row;
    z-index: 1;
    top: 50%;
    left: 0;
    margin-top: -61px;
}

#left-pane {
    width: 200px;
    /* height: 200px; */
    padding: 10px 20px;
    background-color: #24252a;
    border: none;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    color: #edf0f1;
    padding: 10px 20px;

    transition: width 0.5s, padding 0.1s;
    white-space: nowrap;
    overflow: hidden;
}

#left-title {
    font-size: larger;
    font-weight: 700;
}

#steps {
    list-style-type: none;
}

#left-hide-pane {
    margin-top: 25px;
    width: 25px;
    height: 50px;
    background-color: #24252a;
    border: none;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;

    display: flex;
    justify-content: center;
    align-items: center;
}

#left-hide-icon {
    transition: transform 0.5s;
}
